.menu-list {
    display: flex;
    list-style: none;
    flex-direction: column;
    gap: 0.6rem;
    padding: 10px;
}

.menu-list-item {
    padding: 0.5rem;
    border-radius: 5px;
    overflow: hidden;
}

.menu-list-item:hover {
    background: var(--bs-info);
    cursor: pointer;
    transition: all 0.3s ease-in;
}

.menu-list-item.active {
    background: var(--bs-info);
    cursor: pointer;
    transition: all 0.3s ease-in;
}

.menu-list-item a {
    overflow: hidden;
    display: flex;
    gap: 20px;
    align-items: center;
    color: #fff;
}