.carousel-container .carousel-control-prev,
.carousel-container .carousel-control-next {
    opacity: 0;
    transition: opacity 0.3s ease;
}

.carousel-container:hover .carousel-control-prev,
.carousel-container:hover .carousel-control-next {
    opacity: 1;
}


.carosel-img {
    width: 100%;
    height: auto;
    object-fit: cover;
}


@media (max-width: 576px) {
    .carosel-img {
        height: 200px;
    }
}


.nav.nav-pills {
    display: none;
}

@media (min-width: 768px) {
    .nav.nav-pills {
        display: inline-flex;
    }
}

@media (max-width: 768px) {

    .col-md-4,
    .col-lg-3,
    .col-xl-2 {
        flex: 0 0 100%;
        max-width: 100%;
    }
}

.small-text {
    font-size: 1.5rem;
    color: black;
}

.small-image{
    height: 250px;
    width: 200;
}

@media (max-width: 576px) {
    .col-6 {
        max-width: 50%;
        flex: 0 0 50%;
    }

    .small-text {
        font-size: 0.9rem;
    }

    .small-image {
        height: 150px;
        width: 150px;
    }
}

@media (max-width: 760px) and (min-width: 580px) {
    .col-6 {
        max-width: 50%;
        flex: 0 0 50%;
    }
}


.banner-image {
    width: 100%;
    height: auto;
    object-fit: cover;
}


@media (max-width: 576px) {
    .banner-image {
        height: 200px;
    }
}


.category-image {
    height: 200px;
}

@media (max-width: 767px) {
    .category-image {
        height: 150px;
    }
}

@media (max-width: 576px) and (min-width: 0px) {
    .add {
        font-size: 0.8rem;
        padding: 10px 10px 10px 10px !important;
    }
}


.carousel-indicators [data-bs-target] {
    background-color: #888;
}

.carousel-indicators .active {
    background-color: #25bbe4;
}


.related-image {
    height: 230px;
}

@media (max-width: 767px) {
    .related-image {
        height: 170px;
    }
}

.related {
    font-size: 0.8rem;
}

@media (max-width: 576px) and (min-width: 0px) {
    .related {
        font-size: 0.5rem;
        padding: 5px 10px 5px 10px !important;
        margin-top: 0px;
    }
}

@media (max-width: 767px) {
    .finalPrice {
        font-size: 0.9rem;
    }
}

.menubar {
    background-color: #cce5fc;
}


.menubar p:hover {
    color: #25bbe4 !important;
}


.product-name {
    font-size: 1.25rem; 
}

@media (max-width: 768px) {
    .product-name {
        font-size: 1rem;
    }
    .rating-star{
        font-size: 0.8rem;
    }
    .best-price{
        font-size: 1rem;
    }
    .best-cart{
        font-size: 0.8rem;
    }
}

@media (max-width: 576px) {
    .product-name {
        font-size: 0.875rem;
    }
    .rating-star{
        font-size: 0.5rem;
    }
    .best-price{
        font-size: 0.8rem;
    }
    .best-cart{
        font-size: 0.5rem;
    }
}


@media (max-width: 576px) {
    .total-price{
        font-size: 0.8rem;
    }
}


.custom-input:focus {
    outline: none; 
    border-color: #4a90e2;
    box-shadow: 0px 0px 2px 1px rgba(74, 144, 226, 0.5); 
    transition: border-color 0.3s ease, box-shadow 0.3s ease;
  }
  
.mobile-cart{
    margin-left: 50px;
}

.login-btn{
    font-size: 12px;
}