.stepper {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;

}

.step {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.step-number {
  width: 25px;
  height: 25px;
  border-radius: 50%;
  background-color: #ccc;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 5px;
  z-index: 2;
}

.step-name {
  font-size: 14px;
}

@media (max-width: 576px) {
  .step-name {
    font-size: 10px;
  }
}

.active .step-number {
  background-color: #007bff;
  color: #fff;
}

.complete .step-number {
  background-color: #28a745;
  color: #fff;
}


.progress-bar {
  position: absolute;
  top: 23%;
  right: 0;
  width: 88%;
  height: 4px;
  background-color: #dcdcdc;
}

@media (max-width: 576px) {
  .progress-bar {
    width: 80%;
  }
}

.progress {
  height: 100%;
  background-color: #28a745;
  transition: width 0.2s ease;
}