.sidebar {

    display: flex;
    flex-direction: column;
    gap: 1rem;
    background: #363535;
    width: 200px;
    color: #fff;
    height: 100vh;
    position: sticky;
    left: 0;
    top: 0;
    z-index: 1;
    transition: width 0.3s ease-in-out;
}

.sidebar--logo {
    text-align: center;
    font-size: 1.5rem;
    font-weight: bold;
padding: 0.8rem;
border-bottom: 1px solid rgb(229, 225, 225);
}

.sidebar-toggle {
    display: none;
}

@media (max-width:500px) {
    .sidebar {
        width: 0;
        overflow: hidden;
    }

    .sidebar.open {
        width: 200px;
        position: fixed;
        top: 0;
        left: 0;
        transition: width 0.3s ease-in-out;
    }

    .sidebar.open .sidebar--logo{
        margin-top: 3rem;
    }

    .sidebar-toggle{
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 1.5rem;
        width: 50px;
        height: 30px;
        border-radius: 4px;
        background: gray;
        color: #fff;
        position: absolute;
        z-index: 2;
        margin: 10px 5px;

    }
}